.spinner {
  text-align: center;
  background: #1e293b;
}

.spinner img {
  width: 12rem;
  height: 12rem;
  margin: 0 auto;
  animation: 2s linear infinite rotate;
  display: block;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
